.sidebar-container {
    position: absolute;
    top: 0;
    left: 100%;
    width: 300px;
    max-width: 75%;
    height: 100vh;

    .sidebar {
        padding-top: 4.5rem;
        height: 100%;
        background-color: #FFFFFF;
        transition: all 800ms;

        &.is-visible {
            transform: translate(-300px, 0px);
        }
    }

    .sidebar-toggle-button {
        position: absolute;
        top: 1rem;
        right: calc(100% + 1rem);
        z-index: 1000;
    }
}
